.navbar {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
  justify-content: space-between;
  align-items: center;
}

.navbar_logo {
  flex-shrink: 1;
  margin-left: 100px;
  margin-right: 10px;
}

.navbar_logo img {
  width: 70px;
}

.navbar_bcharity {
  font-size: 3rem; 
}

.navbar-item-container{
  display:flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  background-color: white;
  box-shadow: 0rem 0rem 0.5rem 0.1rem #88888822;
  height: 60px;
  margin-left: 150px;
  border-radius: 0.5rem;
  flex-shrink: 1;
}

.navbar-item { 
  justify-content: center;
  align-items: center;
  margin-left: 25px;
  margin-right: 25px;
  font-size: 1.5em;
  text-decoration: inherit;
  color: black;
  padding-top: 3px;
  flex-shrink: 1;
}

.navbar-item.navbar-active{
  justify-content: center;
  align-items: center;
  margin-left: 15px;
  margin-right: 25px;
  color: white;
  font-size: 1.5em;
  text-decoration: inherit;
  background: linear-gradient(70deg, var(--blue-gradient-start) 15%, var(--blue-gradient-end));
  padding-top: 3px;
  padding-bottom: 6pm;
  padding-right: 30px;
  padding-left: 30px;

  -webkit-transform: translateY(-15px);

          transform: translateY(-15px);
  border-radius: 0.5rem;
  flex-shrink: 1;
}

.navbar_search input{
    width: inherit;
    font-size: 20px;
    flex-shrink: 1;
}

.navbar_search {
  box-shadow: 0rem 0rem 0.5rem 0.1rem #88888822;
  border-radius: 0.5rem;
  flex-shrink: 1;
  margin-left: 30px;
}

.navbar-login {
  width: 50px;
