.icon-button{
  width: 30px;
  height: 30px;
  border-radius: 50%;
  padding: 5px;
  margin: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon-button svg{
  fill: #dadce1;
  fill: var(--text-color);
  width: 20px;
  height: 20px;
}

.icon-button:hover {
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
}

.dropdown {
  position: absolute;
  top: 70px;
  width: 300px;
  -webkit-transform: translateX(-45%);
          transform: translateX(-45%);
  background-color: white;
  border: 1px solid #474a4d;
  border: var(--border);
  border-radius: 8px;
  border-radius: var(--border-radius);
  padding: 1rem;
  overflow: hidden;
}

.menu-item{
  height: 50px;
  display: flex;
  align-items: center;
  border-radius: 8px;
  border-radius: var(--border-radius);
  transition: background 500ms;
  transition: background var(--speed);
  padding: 0.5rem;
}

.menu-item:hover {
  background-color: red;
}

.icon-right{
  margin-left: auto;
}


